.wrapper{
    width:300px;
    margin: 70px auto;
}

.frm{
    display: flex;
    justify-content: center;
    padding:90px;
    background-color: lightgray;
    box-shadow: 0px 0px 7px grey;
}

input{
    margin:15px;
}

.btn{
    display: flex;
    flex-direction: column;
    text-align: center;
}

button{
    width:80px;
    border:0px solid black;
    border-radius: 20px;
    margin-bottom:20px;
    text-align: center;
    padding:10px;
    background-color: black;
    color:white;
    cursor: pointer;
}
.link{
    color:white;
    text-decoration: none;
}